.device-list {
  margin: 0;
  padding: 0;
}

.device-list__item {
  padding: 20px 0;
  list-style: none;

  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
  color: @bg-header;
}

.device__radio:checked + .device__label,
.device__radio:not(:checked) + .device__label {
  display: inline-block;
  position: relative;
  padding-left: 70px;
  line-height: 35px;
  cursor: pointer;
}

.device__radio:checked + .device__label::before,
.device__radio:not(:checked) + .device__label::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 37px;
  height: 37px;
  border: 2px solid #e5e5e5;
  background-color: #ffffff;
  border-radius: 100%;
}

.device__radio:hover + .device__label::before {
    border: 2px solid @bg-header;
  }

.device__radio:active + .device__label::before {
  border: 2px solid @red;
}


.device__radio:checked + .device__label::after,
.device__radio:not(:checked) + .device__label::after {
  content: "";
  position: absolute;
  left: 11px;
  top: 11px;
  width: 15px;
  height: 15px;
  background-color: @red;
  border-radius: 100%;
  opacity: 1;
}

.device__radio:not(:checked) + .device__label::after {
  opacity: 0;
}

@media (min-width: 660px) {
  .device-list {
    padding-bottom: 55px;
  }

  .device-list__item {
    padding-top: 42px;
    padding-bottom: 0;
    padding-left: 40px;
  }
}

@media (min-width: 960px) {
  .device-list__item {
    padding-left: 50px;
  }
}
